import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Avaliacao_reuniao } from '../../../modelo/Avaliacao_reuniao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'avaliacao_reuniao-dados-edit',   templateUrl: 'avaliacao_reuniao_dadosEdit.component.html' }) export class Avaliacao_reuniaoDadosEditComponent implements OnInit {    avaliacao_reuniao: Avaliacao_reuniao;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.avaliacao_reuniao.Avaliacao_reuniao";                 let id = +params['id'];         this.avaliacao_reuniao = new Avaliacao_reuniao();         this.avaliacao_reuniao.cd_avaliacao_reuniao = id;         o.objetoJson = this.avaliacao_reuniao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a avaliacao_reuniao. ';       }      this.avaliacao_reuniao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


